// 获取全部频道
export const getAllChannels = () => {
  return request({
    url: '/v1_0/channels',
    method: 'GET',
    needToken: true
  })
}

// 修改用户频道，使用PUT；
export const appendChannels = channels => {
  return request({
    url: '/v1_0/user/channels',
    method: 'PUT',
    data: {
      channels: channels
    },
    needToken: true
  })
}
// 删除频道
export const delChannel = channelId => {
  return request({
    url: '/v1_0/user/channels/' + channelId,
    method: 'DELETE',
    needToken: true
  })
}
// 用户不感兴趣的文章
export const dislike = articleID => {
  return request({
    method: 'POST',
    url: 'v1_0/article/dislikes',
    data: {
      target: articleID
    },
    needToken: true
  })
}
// 举报文章
export const report = (articleID, typeID) => {
  return request({
    method: 'POST',
    url: 'v1_0/article/reports',
    data: {
      target: articleID,
      type: typeID
    },
    needToken: true
  })
}
