      justify-content: center;
      .like,
      .dislike {
        display: flex;
        justify-content: center;
        align-items: center;
        .likeMore {
          margin-right: 5px;
        }
        .moreIcon {
          font-size: 24px;
        }
        margin-right: 14px;
        height: 40px;
        line-height: 40px;
        width: 120px;
        font-size: 14px;
        color: #a0a0a0;
        border: 3px solid #eeeeee;
        border-radius: 25px;
      }
      .active {
        border: 1px solid #e22829;
