      img {
        width: 318px;
        height: 176px;
        border-radius: 5px;
      }
      .title {
        margin-top: 8px;
        font-size: 14px;
        color: #222222;
        line-height: 20px;
        letter-spacing: 1px;
      }
    }
  }
  .comment {
    .items {
      padding: 15px 15px 0;
    }
  }
  .text {
