    width: 100%;
    background-color: #fff;
    height: 60px;
    box-shadow: 0px -2px 5px 0px rgba(0, 0, 0, 0.04);
    .myInput {
      // margin-right: 15px;
      flex: 1;
      padding: 0;
      height: 40px;
      border-radius: 20px 0 0 20px;
      border: 1px solid #cccccc;
      line-height: 40px;
      font-size: 14px;
      padding: 0 15px;
    }
    .myBTN {
      width: 80px;
      height: 42px;
      font-size: 16px;
