      position: relative;
      padding: 0;
      display: flex;
      align-items: center;
      .title {
        position: absolute;
        left: 50%;
        top: 0;
        transform: translateX(-50%);
        flex: 1;
        font-size: 18px;
      }
    }
    .allRepaly {
      margin-top: 5px;
      padding: 15px 0 5px;
      font-size: 16px;
      color: #262626;
      border-top: 1px solid #e8e8e8;
    }
  }
  .more {
    border-radius: 35px 0 0 35px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    .myMoreBTN {
      margin: 10px 0;
      position: relative;
      display: flex;
      flex-direction: column;
      text-align: center;
      justify-content: center;
      width: 100px;
      height: 100px;
      background-color: rgb(255, 208, 0);
      color: white;
      border-radius: 20px 35px 20px 35px;
