    .likeActive {
      background-color: #fff;
      border: 5px solid #07c3f2;
      color: #03a9d3;
    }
    .dislikeActive {
      background-color: #fff;
      border: 5px solid #fe2857;
      color: #df1541;
    }
    .cancelCollect {
      background-color: #fff;
      border: 5px solid rgb(255, 208, 0);
      color: rgb(247, 201, 0);
    }
  }
}
</style>
